import {Injectable} from '@angular/core';
import {ActionSheetController} from "ionic-angular";
import { Camera, CameraOptions } from '@ionic-native/camera';
import {ImagePicker} from "@ionic-native/image-picker";
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import {UtilService} from "./util.service";

@Injectable()
export class ImageService {
    // 
     private cameraOpt: CameraOptions = {
        quality: 50,
        destinationType: 1, // Camera.DestinationType.FILE_URI,
        sourceType: 1, // Camera.PictureSourceType.CAMERA,
        encodingType: 0, // Camera.EncodingType.JPEG,
        mediaType: 0, // Camera.MediaType.PICTURE,
        allowEdit: true,
        correctOrientation: true
    }

    // 
    private imagePickerOpt = {
        maximumImagesCount: 1,//
        width: 800,
        height: 800,
        quality: 80
    };

    upload: any = {
        url: '',           //url
        fileKey: 'file',                                       //key
        headers: {
            'Accept': 'text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,*/*;q=0.8' // 
        },
        params: {},             //
        success: (data) => {

        },  //
        error: (err) => {},     //
        listen: () => {}        //
    };

    constructor(
        public actionSheetCtrl: ActionSheetController,
        private camera: Camera,
        private imagePicker: ImagePicker,
        private utilService: UtilService,
        private transfer: FileTransfer
    ) {

    }

    showPicActionSheet() {
        this.useASComponent();
    }

    // ionicActionSheet
    private useASComponent() {
        let actionSheet = this.actionSheetCtrl.create({
            title: '',
            buttons: [
                {
                    text: '',
                    handler: () => {
                        this.startCamera();
                    }
                },
                {
                    text: '',
                    handler: () => {
                        this.openImgPicker();
                    }
                },
                {
                    text: '',
                    role: 'cancel',
                    handler: () => {

                    }
                }
            ]
        });
        actionSheet.present();
    }

    // 
    private startCamera() {
        this.camera.getPicture(this.cameraOpt).then((imageData) => {

            this.uploadImg(imageData);

        }, (err) => {
            this.utilService.toast(': '); //
        });
    }

    // 
    private openImgPicker() {
        let temp = '';
        this.imagePicker.getPictures(this.imagePickerOpt)
            .then((results) => {
                for (var i = 0; i < results.length; i++) {
                    temp = results[i];
                }

                this.uploadImg(temp);

            }, (err) => {
                this.utilService.toast(': '); //
            });

        /*let str = '{"status":1,"msg":"","data":"http:\/\/192.168.1.20\/image\/580af6bcc4d40580af6bcc4d45.jpg"}';
        let res = JSON.parse(str);
        this.upload.success(res);*/
    }

    public fileTransfer: FileTransferObject = this.transfer.create();
    // 
    private uploadImg(path: string) {
        if (!path) {
            return;
        }

        let options: FileUploadOptions = {
            fileKey: this.upload.fileKey,
            headers: this.upload.headers,
            params: this.upload.params
        };

        this.fileTransfer.upload(path, this.upload.url, options)
            .then((data) => {

                if (this.upload.success) {
                    this.upload.success(JSON.parse(data.response));
                }

            }, (err) => {
                if (this.upload.error) {
                    this.upload.error(err);
                } else {
                    this.utilService.toast('');
                }
            });
    }

    // 
    stopUpload() {
        if (this.fileTransfer) {
            this.fileTransfer.abort();
        }
    }
}



